
@import '../../../static/css/icon-style.css';
.search-wrapper {
  position: fixed;
  z-index: 101;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  background: #fff;
}
.search-wrapper .search-header-wrapper {
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  width: 100%;
  height: 40px;
}
.search-wrapper .search-header-wrapper .back-icon {
  -ms-flex: 0 0 20px;
      flex: 0 0 20px;
  font-size: 18px;
  padding: 5px;
  margin-top: 2px;
}
.search-wrapper .search-input-wrapper {
  -ms-flex: 1;
      flex: 1;
  display: inline-block;
  position: relative;
  padding-left: 4px;
  font-size: 0;
  padding-right: 30px;
}
.search-wrapper .search-input-wrapper .search-input {
  font-size: 16px;
  width: 100%;
  padding: 7px;
  background: #efefee;
  border-radius: 15px;
  outline: none;
}
.search-wrapper .search-input-wrapper .search-icon-warpper {
  position: absolute;
  right: 0;
  top: 0;
  padding-right: 30px;
}
.search-wrapper .search-input-wrapper .search-icon-warpper .search-icon {
  padding: 5px;
  height: 20px;
}
.search-wrapper .line {
  display: block;
  height: 10px;
  background: #efefee;
  position: relative;
}
.search-wrapper .line::after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.search-wrapper .search-text-wrapper {
  width: 100%;
  font-size: 0;
}
.search-wrapper .search-text-wrapper .text-item-wrapper {
  display: inline-block;
  height: 50px;
  width: 33%;
  text-align: center;
  border-left: 1px solid rgba(7,17,27,0.1);
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.search-wrapper .search-text-wrapper .text-item-wrapper .text {
  display: inline-block;
  font-size: 16px;
  padding: 10px 5px;
  line-height: 30px;
  color: #000;
}


