
@import '../../../static/css/icon-style.css';
.address-wrapper {
  position: fixed;
  z-index: 101;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  background: #fff;
}
.address-wrapper .address-header {
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  line-height: 40px;
  font-size: 0;
  padding: 10px 0 5px 10px;
  position: relative;
}
.address-wrapper .address-header::after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom: 1px solid rgba(7,17,27,0.3);
  content: ' ';
}
.address-wrapper .address-header .back-icon {
  display: inline-block;
  -ms-flex: 0 0 20px;
      flex: 0 0 20px;
  line-height: 40px;
  vertical-align: top;
  font-size: 18px;
}
.address-wrapper .address-header .address-title {
  -ms-flex: 1;
      flex: 1;
  text-align: center;
  font-size: 18px;
  font-weight: 500;
}
.address-wrapper .choice-city-text {
  display: inline-block;
  padding-left: 10px;
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  color: #7e8c8d;
}
.address-wrapper .city-wrapper {
  display: inline-block;
  height: 60px;
  width: 100%;
  font-size: 0;
  background: #efefee;
}
.address-wrapper .city-wrapper .city-text-wrapper {
  position: relative;
  display: inline-block;
  height: 20px;
  margin-left: 30px;
  line-height: 70px;
}
.address-wrapper .city-wrapper .city-text-wrapper .city-text {
  font-size: 16px;
  text-align: center;
  line-height: 20px;
  padding: 8px 16px;
  background: #fff;
  border-radius: 5px;
}
.address-wrapper .city-wrapper .city-text-wrapper .city-icon-wrapper {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  right: 10px;
  top: -10px;
  background: #7fffd4;
  border-radius: 50%;
}
.address-wrapper .city-wrapper .city-text-wrapper .city-icon-wrapper .city-icon {
  position: absolute;
  display: inline-block;
  font-size: 14px;
  color: #fff;
  padding: 5px 0 0 2px;
}
.address-wrapper .total-city-text {
  display: block;
  font-size: 16px;
  padding: 15px 0 15px 10px;
  color: #7e8c8d;
  background: #fff;
}
.address-wrapper .citys-wrapper {
  display: inline-block;
  overflow-y: auto;
  width: 100%;
  background: #eee;
}
.address-wrapper .citys-wrapper .citys-item {
  display: inline-block;
  width: 33%;
  height: 40px;
  text-align: center;
  margin: 20px 0 0 0;
}
.address-wrapper .citys-wrapper .citys-item .city-text {
  -ms-flex: 1;
      flex: 1;
  display: inline-block;
  padding: 8px 16px;
  font-size: 15px;
  background: #fff;
  line-height: 20px;
  text-align: center;
  border-radius: 5px;
}


