
.menus-wrapper {
  position: fixed;
  width: 100%;
  height: 40px;
  background-color: #fff;
}
.menus-wrapper .menus {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.menus-wrapper .menus .menus-item {
  -ms-flex: 1;
      flex: 1;
  text-align: center;
  line-height: 40px;
}
.menus-wrapper .menus .menus-item .menu-text {
  font-size: 15px;
  color: #787871;
}
.menus-wrapper .menus .menus-item .active {
  color: #42bd56;
}
.menus-wrapper .menu-item.menu-render-enter-active,
.menus-wrapper .menu-item.menu-render-leave-active {
  transition: opacity 0.3s linear;
}
.menus-wrapper .menu-item.menu-render-enter,
.menus-wrapper .menu-item.menu-render-leave-active {
  opacity: 0;
}


