
@import '../../../static/css/icon-style.css';
.item-wrapper {
  display: -ms-flexbox;
  display: flex;
  font-size: 0;
  width: 94%;
  padding: 10px;
  height: 30px;
  position: relative;
}
.item-wrapper::after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.item-wrapper .item-icon-wrapper {
  -ms-flex: 0 0 30px;
      flex: 0 0 30px;
  padding: 0 5px;
  margin-top: 3px;
}
.item-wrapper .item-icon-wrapper img {
  height: 20px;
}
.item-wrapper .item-text {
  -ms-flex: 1;
      flex: 1;
  font-size: 18px;
  line-height: 30px;
}
.item-wrapper .right-icon {
  position: absolute;
  -ms-flex: 0 0 20px;
      flex: 0 0 20px;
  display: inline-block;
  font-size: 25px;
  line-height: 30px;
  right: 0;
  color: #efe f;
}


