
.mine-wrapper .header-wrapper {
  position: relative;
  height: 240px;
  width: 100%;
}
.mine-wrapper .header-wrapper .setting-icon {
  position: absolute;
  right: 0;
  top: 0;
  margin: 5px 5px 0 0;
  padding: 5px;
}
.mine-wrapper .header-wrapper .setting-icon img {
  height: 20px;
  width: 20px;
}
.mine-wrapper .header-wrapper .user-image {
  display: inline-block;
  position: absolute;
  text-align: center;
  width: 100%;
  height: 100%;
  margin-top: 50px;
}
.mine-wrapper .header-wrapper .user-image img {
  height: 100px;
  border-radius: 50%;
}
.mine-wrapper .header-wrapper .header-text-wrapper {
  display: inline-block;
  position: absolute;
  bottom: 0;
  height: 12px;
  width: 100%;
  text-align: center;
  margin-bottom: 20px;
/* opacity: 0.8 */
}
.mine-wrapper .header-wrapper .header-text-wrapper .header-text {
  display: inline-block;
  font-size: 14px;
  color: #eee;
}
.mine-wrapper .content-wrapper {
  height: 200px;
}


