
#recommend {
  padding-top: 50px;
/* 专题 */
}
#recommend .category {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 70px;
  background-color: #fff;
  overflow: hidden;
  position: relative;
}
#recommend .category::after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
#recommend .category .category-item {
  -ms-flex: 1;
      flex: 1;
  position: relative;
  text-align: center;
}
#recommend .category .category-item-icon {
  display: block;
  margin-top: 8px;
  width: 100%;
}
#recommend .category .category-item-icon img {
  display: inline-block;
  height: 35px;
  width: 35px;
}
#recommend .category .category-item-text {
  display: inline-block;
  margin-top: 5px;
  bottom: 0;
  font-size: 12px;
  color: rgba(0,0,0,0.4);
}
#recommend .subject {
  border-top: 10px solid #eee;
  border-bottom: 10px solid #eee;
  padding: 8px;
  background: #fff;
  position: relative;
}
#recommend .subject::after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
#recommend .subject .subject-title {
  display: block;
  font-size: 0;
  height: 15px;
}
#recommend .subject .subject-title .subject-title-icon {
  vertical-align: top;
  height: 14px;
  width: 14px;
}
#recommend .subject .subject-title .subject-title-text {
  line-height: 15px;
  margin-left: 10px;
  font-size: 14px;
  color: #000;
}
#recommend .subject .subject-img {
  margin-top: 10px;
}
#recommend .subject .subject-img img {
  width: 100%;
  height: 160px;
}
#recommend .recommend-wrapper {
  margin-bottom: 52px;
}
#recommend .recommend-wrapper .recommend-header-wrapper {
  display: block;
  height: 20px;
  font-size: 0;
  padding: 10px 0 10px 10px;
  position: relative;
}
#recommend .recommend-wrapper .recommend-header-wrapper::after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
#recommend .recommend-wrapper .recommend-header-wrapper .recommend-header-icon {
  display: inline-block;
  width: 18px;
  vertical-align: top;
}
#recommend .recommend-wrapper .recommend-header-wrapper .recommend-header-text {
  display: inline-block;
  margin-left: 10px;
  line-height: 20px;
  font-size: 16px;
}


