
.header_wrapper {
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  top: 0;
  height: 50px;
  width: 100%;
  font-size: 0;
  border-bottom: 1px solid #7e8c8d;
  background: #44bb8c;
  z-index: 30;
}
.header_wrapper .adderss-wrapper {
  position: absolute;
  -ms-flex: 0 0 70px;
      flex: 0 0 70px;
  vertical-align: top;
  text-align: center;
}
.header_wrapper .adderss-wrapper .address-text {
  line-height: 50px;
  padding-left: 10px;
  font-size: 16px;
  color: #fff;
}
