}
.header_wrapper .header-title {
  -ms-flex: 1;
      flex: 1;
  display: inline-block;
  margin-left: 40px;
  text-align: center;
}
.header_wrapper .header-title img {
  height: 20px;
  margin-top: 15px;
}
.header_wrapper .header-search {
  -ms-flex: 0 0 40px;
      flex: 0 0 40px;
  text-align: center;
}
.header_wrapper .header-search img {
  height: 20px;
  margin-top: 15px;
}


