
.vue-only-slider {
  width: 100%;
  position: relative;
  text-align: center;
  overflow: hidden;
}
.vue-only-slider ul {
  width: 100%;
  overflow: hidden;
}
.vue-only-slider li {
  position: absolute;
}
.vue-only-slider a,
.vue-only-slider img {
  display: block;
  width: 100%;
  -webkit-touch-callout: none;
}
.vue-only-slider .pagination {
  z-index: 2;
  line-height: 1;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 8px;
}
.vue-only-slider .pagination i {
  display: inline-block;
  width: 10px;
  height: 10px;
  overflow: hidden;
  border-radius: 100%;
  background: rgba(255,255,255,0.3);
  margin: 0 3px;
  text-indent: -9999px;
}
.vue-only-slider .pagination i.on {
  background-color: #44bb8c;
}


