
@import '../../../static/css/icon-style.css';
.detail-wrapper {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 50;
  background: #fff;
  height: 100%;
  width: 100%;
}
.detail-wrapper .header-wrapper {
  display: -ms-flexbox;
  display: flex;
  height: 50px;
  background: #44bb8c;
}
.detail-wrapper .header-wrapper .back-icon {
  -ms-flex: 0 0 40px;
      flex: 0 0 40px;
  line-height: 50px;
  text-align: center;
  font-size: 18px;
  color: #fff;
}
.detail-wrapper .header-wrapper .header-icon {
  -ms-flex: 1;
      flex: 1;
  display: inline-block;
  text-align: center;
  line-height: 50px;
  margin-right: 40px;
}
.detail-wrapper .header-wrapper .header-icon .header-img {
  height: 20px;
  margin-top: 15px;
}
.detail-wrapper .banner-wrapper {
  display: block;
