}
.detail-wrapper .banner-wrapper .banner-img {
  width: 100%;
  height: 230px;
}
.detail-wrapper .title-wrapper {
  font-size: 20px;
  padding: 10px 5px 20px 5px;
  position: relative;
}
.detail-wrapper .title-wrapper::after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom: 1px solid rgba(7,17,27,0.2);
  content: ' ';
}
.detail-wrapper .title-wrapper .user-wrapper {
  display: -ms-flexbox;
  display: flex;
  height: 20px;
