  margin: 10px 0 10px 10px;
}
.detail-wrapper .title-wrapper .user-wrapper .image-wrapper {
  -ms-flex: 0 0 40px;
      flex: 0 0 40px;
  border-radius: 50%;
}
.detail-wrapper .title-wrapper .user-wrapper .user-image {
  display: inline-block;
  height: 40px;
  width: 40px;
  border-radius: 50%;
}
.detail-wrapper .title-wrapper .user-wrapper .user-name {
  -ms-flex: 1;
      flex: 1;
