  content: ' ';
}
.detail-wrapper .time-wrapper .icon,
.detail-wrapper .price-wrapper .icon {
  -ms-flex: 0 0 50px;
      flex: 0 0 50px;
}
.detail-wrapper .time-wrapper .text,
.detail-wrapper .price-wrapper .text {
  -ms-flex: 1;
      flex: 1;
  line-height: 60px;
}
.detail-wrapper .localtion-wrapper {
  display: -ms-flexbox;
  display: flex;
  height: 60px;
  position: relative;
  padding-top: 20px;
}
.detail-wrapper .localtion-wrapper::after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom: 1px solid rgba(7,17,27,0.2);
  content: ' ';
}
.detail-wrapper .localtion-wrapper .icon {
  -ms-flex: 0 0 50px;
      flex: 0 0 50px;
