
.m-tabbar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  position: fixed;
  z-index: 30;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 50px;
  overflow: hidden;
  clear: both;
  background: #fff;
}
.m-tabbar::before {
  content: ' ';
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  background: #e4e4e4;
  transform: scaleY(0.5);
}


