
.content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  height: 85px;
  background-color: #fafafa;
  position: relative;
}
.content::after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-bottom: 1px solid #eee;
  content: ' ';
}
.content .left {
  -ms-flex: 0 0 100px;
      flex: 0 0 100px;
  height: 120px;
}
.content .left img {
  padding: 10px;
  width: 110px;
  height: 65px;
}
.content .right {
  position: relative;
  display: inline-block;
  -ms-flex: 1;
      flex: 1;
  font-size: 0;
  height: 100%;
  padding-right: 5px;
}
.content .right .right-title {
  display: block;
  font-size: 14px;
  font-weight: 500;
  margin-top: 10px;
  color: #323232;
}
.content .right .right-location {
  display: inline-block;
  width: 20em;
  margin-top: 5px;
  font-size: 12px;
  color: #8f8f8f;
}
.content .right .right-time {
  position: absolute;
  left: 0;
  bottom: 10px;
  font-size: 12px;
  color: #8f8f8f;
}
.content .right .right-price {
  position: absolute;
  margin-top: 5px;
  font-size: 10px;
  color: #008000;
  right: 0;
  bottom: 10px;
  margin-right: 10px;
}
